@charset "utf-8";
@import "../includes/zindex";

.line {
  width: 90.4%;
  height: .8vw;
  margin: 0 auto;
  background: #000;
}
.scr-navi {
  .swiper-wrapper {
    .swiper-slide {
      a {
        font-size: 4vw;
      }
    }
  }
}
.order {
  margin-top: 6.666vw;
  .d-info {
    padding: 0 4.8vw;
    li {
      overflow: hidden;
      font-size: 3.733vw;
      padding: 4.8vw 0;
      border-bottom: .266vw #f1f1f1 solid;
      line-height: 5.333vw;
      .tit {
        float: left;
        width: 23.6vw;
        color: #898989;
      }
      .cont {
        float: left;
        width: 66.8vw;
        color: #1f1f1f;
        input {
          font-size: 3.733vw;
          width: 100%;
        }
        .btn {
          float: right;
          font-size: 3.466vw;
          color: #1f1f1f;
          border: .266vw #f1f1f1 solid;
          padding: 2vw;
        }
      }
    }
  }
  .d-top {
    padding: 6.666vw 0;
    overflow: hidden;
  }
  .gift {
    margin-bottom: 9.6vw;
    .d-top {
      width: 90.4vw;
      margin: 9.6vw auto;
    }
    .top {
      padding: 5.333vw 0;
      margin: 0 auto;
      overflow: hidden;
      width: 90.4vw;
      border-top: .266vw #f1f1f1 solid;
      border-bottom: .266vw #f1f1f1 solid;
      h4 {
        font-size: 3.733vw;
        color: #1f1f1f;
        float: left;
      }
      .more {
        font-size: 0;
        text-indent: -999px;
        display: block;
        width: 3.066vw;
        height: 1.733vw;
        background: url('/_ui/img/m/icn/icn-arrow04.png') no-repeat;
        background-size: 100%;
        float: right;
        margin-top: 1.333vw;
      }
      .more.on {
        -webkit-transform: rotate(-180deg);
        -moz-transform: rotate(-180deg);
        -o-transform: rotate(-180deg);
        -ms-transform: rotate(-180deg);
        transform: rotate(-180deg);
      }
    }
    .gift-choice {
      display: none;
      background: #f7f7f7;
      padding: 8.8vw 0;
      li .tit {
        font-size: 3.466vw;
        color: #1f1f1f;
      }
      .list {
        margin-top: 4vw;
        li {
          background: #fbfbfb;
          overflow: hidden;
          margin-top: 2.933vw;
          cursor: pointer;
          &:first-child {
            margin-top: 0;
          }
        }
        li.select {
          background: #fff url('/_ui/img/m/icn/icn-check03.png') no-repeat 82.933vw 9.866vw;
          background-size: 2.933vw 2.133vw;
        }
        .prod {
          width: 21.866vw;
          float: left;
          margin-right: 4vw;
        }
        .info {
          margin-top: 6.933vw;
          .t1 {
            color: #1f1f1f;
            font-size: 2.666vw;
            font-weight: 700;
          }
          .t2 {
            color: #898989;
            font-size: 2.666vw;
            margin-top: 2vw;
          }
        }
      }
    }
    .gift-choice>li {
      padding: 0 4.8vw;
      margin-top: 9.333vw;
      &:first-child {
        margin-top: 0;
      }
    }
    .d-gift {
      display: none;
    }
  }
}
.complete {
  padding: 0 4.8vw 16vw;
  .pop-close {
    display: block;
    width: 3.733vw;
    height: 3.733vw;
    background: url('/_ui/img/m/icn/icn-close.png') no-repeat;
    font-size: 0;
    text-indent: -999px;
    position: absolute;
    top: 5.333vw;
    right: 4.8vw;
    background-size: 100%;
  }
  h1 {
    line-height: 14.4vw;
    text-align: center;
    font-size: 4.533vw;
    color: #000;
    font-weight: 700;
  }
  .box1 {
    text-align: center;
    padding-bottom: 12.266vw;
    h3 {
      padding-top: 23.066vw;
      background: url('/_ui/img/m/icn/icn-order.png') no-repeat center top;
      background-size: 12.8vw 15.066vw;
      font-size: 5.333vw;
      color: #000;
      margin-bottom: 10.666vw;
      line-height: 8vw;
    }
    h3 + div {
      font-size: 4.266vw;
      p {
        display: inline-block;
        margin-right: 7.6vw;
        i {
          color: #f23d4e;
        }
      }
      a {
        display: inline-block;
        text-decoration: underline;
        color: #898989;
      }
    }
    div + p {
      margin-top: 11.066vw;
      color: #898989;
      font-size: 3.733vw;
      line-height: 4.933vw;
      a {
        color: #898989;
      }
    }
  }
  .box2 {
    border-top: .8vw #000 solid;
    padding-top: 6.4vw;
    h3 {
      font-size: 4vw;
      color: #1f1f1f;
    }
    .dl-table {
      margin-top: 8.8vw;
      font-size: 3.733vw;
      overflow: hidden;
      border-bottom: .266vw #f1f1f1 solid;
      dl {
        overflow: hidden;
      }
      dt {
        color: #898989;
        height: 10.666vw;
        line-height: 10.666vw;
        width: 33.333%;
        float: left;
        border-top: .266vw #f1f1f1 solid;
        clear: both;
      }
      .dt {
        text-align: left;
        color: #898989;
      }
      dd {
        color: #1f1f1f;
        height: 10.266vw;
        line-height: 10.266vw;
        width: 33.333%;
        float: left;
        text-align: right;
        border-top: .266vw #f1f1f1 solid;
        i {
          color: #898989;
        }
      }
      dt:first-child {
        border-top: 0;
      }
      dt:first-child + dd {
        border-top: 0;
      }
      .bd0 {
        border-top: 0;
      }
      .l2 {
        line-height: 5.333vw;
        padding: 1.866vw 0;
      }
      .l3 {
        line-height: 5.333vw;
        padding: 2.666vw 0;
        height: auto;
      }
      .space {
        border: 0;
      }
      .tl {
        text-align: left;
      }
      .ha {
        height: auto;
      }
      dl > dt:first-child {
        border-top: 0;
      }
      dl > dt:first-child + dd {
        border-top: 0;
      }
      .colspan {
        width: 66.666%;
      }
      .rowspan {
        height: auto;
      }
      .rowspan + dd {
        height: auto;
        width: 66.666%;
        dt {
          width: 50%;
          text-align: left;
        }
        dd {
          width: 50%;
        }
      }
      .deposit-info {
        color: #898989;
      }
    }
    .dl-table.bd0 {
      border-bottom: 0;
    }
    .virtual-info {
      float: right;
      margin-top: 1.6vw;
      color: #898989;
      white-space: nowrap;
    }
    .total {
      margin-top: 6.666vw;
      font-size: 4.533vw;
      overflow: hidden;
      dt {
        float: left;
        color: #1f1f1f;
      }
      dd {
        float: right;
        color: #f23d4e;
        font-weight: 700;
        i {
          color: #1f1f1f;
        }
      }
    }
    .btns {
      font-size: 0;
      letter-spacing: 0;
      word-spacing: 0;
      margin-top: 13.733vw;
      a {
        display: inline-block;
        width: 44vw;
        height: 10.933vw;
        border: .266vw #f1f1f1 solid;
        line-height: 10.933vw;
        box-sizing: border-box;
        text-align: center;
        font-size: 3.466vw;
        color: #1f1f1f;
      }
      a + a {
        margin-left: 2.4vw;
      }
    }
  }
}
.complete.return .box2 {
  border-top: 0;
}
.mypage-order.pt0 {
  padding-top: 0;
}
.mypage-order {
  background: #f3f4f7;
  padding-top: 5.2vw;
  padding-bottom: 5.2vw;
  .my-order-check {
    width: 80.266vw;
    margin: 0 auto;
    background: #fff;
    padding: 5.066vw 5.066vw 6.666vw;
    .tit-top {
      overflow: hidden;
      margin-bottom: 3.2vw;
      h3 {
        font-weight: 700;
        font-size: 4vw;
        color: #1f1f1f;
        float: left;
      }
      .btn-guide {
        float: right;
        font-size: 3.466vw;
        color: #1f1f1f;
        background: url('/_ui/img/m/icn/icn-size-check.png') no-repeat right center;
        background-size: 3.2vw 3.2vw;
        padding-right: 4.666vw;
      }
    }
    p {
      font-size: 3.466vw;
      color: #aaa;
      line-height: 4.533vw;
      margin-bottom: 4.666vw;
      letter-spacing: -.5px;
    }
    .cs-depth3 {
      padding: 0;
      ul {
        margin: 5.866vw 0 6.8vw;
      }
    }
    .btn-inquiry {
      text-align: center;
      a {
        display: block;
        padding: 2.4vw 0 3.066vw;
        color: #1f1f1f;
        font-size: 4.266vw;
        border: .266vw solid #f1f1f1;
      }
    }
    select {
      background: #fff url('/_ui/img/m/icn/icn-arrow02.png') no-repeat 99% 50%;
      background-size: 3vw;
      font-size: 3.733vw;
      line-height: 10.8vw;
      border-bottom: .266vw #eee solid;
    }
    .sct-box {
      overflow: hidden;
      select {
        float: left;
        width: 36.8vw;
      }
      select + select {
        margin-left: 5.6vw;
      }
    }
    .add-prod {
      margin-top: 3.866vw;
      overflow: hidden;
      select {
        width: 19.066vw;
        float: left;
      }
      input {
        width: 56.133vw;
        margin-left: 4.266vw;
        border-bottom: .266vw #eee solid;
        line-height: 10.8vw;
        font-size: 3.733vw;
      }
    }
  }
  .order-number {
    padding: 10.933vw 0 8.266vw;
    text-align: center;
    p {
      font-size: 4.533vw;
      color: #1f1f1f;
      i {
        color: #898989;
      }
    }
  }
  .order {
    padding-bottom: 0;
    margin-top: 3.466vw;
    .d-info {
      background: #fff;
      li .cont {
        color: #898989;
      }
      li .name {
        color: #1f1f1f;
      }
      li:last-child {
        border-bottom: 0;
        padding: 4.8vw 0 11.6vw;
      }
    }
    .d-top {
      border-top: 0;
      h3 {
        font-size: 4vw;
        color: #1f1f1f;
        float: left;
        font-weight: 700;
      }
    }
  }
  .complete {
    background: #fff;
    padding: 0 4.8vw 9.2vw;
    .box2 h3 {
      font-weight: 700;
    }
    &.return-delivery {
      .dl-table {
        border-bottom: 0;
        dd {
          font-family: 'helvetica';
          color: #898989;
          text-align: left;
          &.l3 {
            line-height: 6vw;
          }
        }
      }
    }
  }
  .btn-back {
    text-align: center;
    background: #fff;
    padding-bottom: 6vw;
    a {
      display: block;
      width: 90.4vw;
      height: 10.666vw;
      border: .266vw #f1f1f1 solid;
      font-size: 3.466vw;
      color: #1f1f1f;
      margin: 0 auto;
      line-height: 10.666vw;
    }
  }
  .order.return-pickup {
    margin-top: 0;
    .d-top {
      border-top: .8vw #000 solid;
    }
  }
  .order.cancel-gift {
    background: #f7f7f7;
    margin-top: 0;
    .d-top {
      padding: 9.2vw 4.8vw 8.533vw;
      h3 {
        float: none;
      }
      p {
        float: none;
        color: #898989;
        font-size: 3.2vw;
        margin-top: 2.266vw;
        letter-spacing: -.133vw;
      }
    }
    .gift {
      margin-bottom: 0;
      .gift-choice {
        display: block;
        padding-top: 0;
      }
    }
  }
  .more {
    width: 7.733vw;
    height: 7.733vw;
    display: block;
    background: url('/_ui/img/m/icn/icn-more.png') no-repeat;
    background-size: 100%;
    font-size: 0;
    text-indent: -999px;
    margin: 2.166vw auto 1.466vw auto;
  }
}
.order-notice {
  min-height: 154.4vw;
  h3 {
    font-size: 3.733vw;
    color: #1f1f1f;
    font-weight: 700;
    margin: 7.333vw 0 6.266vw;
    padding: 0 4.8vw;
  }
  .notice {
    padding: 5.866vw 7.466vw 8.533vw;
    li {
      list-style: disc;
      font-size: 3.466vw;
      color: #898989;
      line-height: 5.866vw;
      letter-spacing: -.133vw;
    }
  }
}
.h1-top {
  position: relative;
  .pop-close {
    background: url('/_ui/img/m/icn/icn-close.png') no-repeat;
    background-size: 100%;
    top: 5.333vw;
    right: 4.8vw;
    width: 4vw;
    height: 4vw;
    font-size: 0;
    text-indent: -9999px;
    display: block;
    position: absolute;
  }
  .pop-close.close2 {
    top: 6.4vw;
  }
  h1 {
    font-size: 4.533vw;
    color: #1f1f1f;
    text-align: center;
    font-weight: 700;
    padding: 5.333vw 0 0;
  }
}
@media (min-width: 750px) {
  .line {
    height: 6px;
  }
  .scr-navi {
    width: 678px;
    .swiper-wrapper {
      .swiper-slide a {
        height: 100px;
        line-height: 100px;
        font-size: 30px;
      }
    }
    .swiper-wrapper + .icn-navi {
      width: 35px;
      height: 115px;
      right: -36px;
    }
    .swiper-wrapper + .icn-navi.left {
      left: -36px;
    }
  }
  .order {
    margin-top: 50px;
    .d-info {
      padding: 0 36px;
      li {
        font-size: 28px;
        padding: 36px 0;
        border-bottom: 2px #f1f1f1 solid;
        line-height: 40px;
        .tit {
          width: 177px;
        }
        .cont {
          width: 501px;
          input {
            font-size: 28px;
          }
          .btn {
            font-size: 26px;
            border: 2px #f1f1f1 solid;
            padding: 15px;
          }
        }
      }
    }
    .d-top {
      padding: 50px 0;
    }
    .gift {
      margin-bottom: 72px;
      .d-top {
        width: 678px;
        margin-top: 72px;
      }
      .top {
        padding: 40px 0;
        width: 678px;
        border-top: 2px #f1f1f1 solid;
        border-bottom: 2px #f1f1f1 solid;
        h4 {
          font-size: 28px;
        }
        .more {
          width: 23px;
          height: 13px;
          margin-top: 10px;
        }
      }
      .gift-choice {
        padding: 66px 0;
        li .tit {
          font-size: 26px;
        }
        .list {
          margin-top: 30px;
          li {
            margin-top: 22px;
          }
          li.select {
            background: #fff url('/_ui/img/m/icn/icn-check03.png') no-repeat 622px 74px;
            background-size: 22px 16px;
          }
          .prod {
            width: 164px;
            margin-right: 30px;
          }
          .info {
            margin-top: 52px;
            .t1 {
              font-size: 20px;
              font-weight: 700;
            }
            .t2 {
              font-size: 26px;
              margin-top: 15px;
            }
          }
        }
      }
      .gift-choice>li {
        padding: 0 36px;
        margin-top: 70px;
      }
    }
  }
  .complete {
    padding: 0 36px 120px;
    .pop-close {
      width: 28px;
      height: 28px;
      top: 40px;
      right: 36px;
    }
    h1 {
      line-height: 108px;
      font-size: 34px;
    }
    .box1 {
      padding-bottom: 92px;
      h3 {
        padding-top: 173px;
        background-size: 96px 113px;
        font-size: 40px;
        margin-bottom: 80px;
        line-height: 60px;
      }
      h3 + div {
        font-size: 32px;
        p {
          margin-right: 57px;
        }
      }
      div + p {
        margin-top: 83px;
        font-size: 28px;
        line-height: 37px;
      }
    }
    .box2 {
      border-top: 6px #000 solid;
      padding-top: 48px;
      h3 {
        font-size: 30px;
      }
      .dl-table {
        margin-top: 66px;
        font-size: 28px;
        border-bottom: 2px #f1f1f1 solid;
        dt {
          height: 80px;
          line-height: 80px;
          border-top: 2px #f1f1f1 solid;
        }
        dd {
          height: 80px;
          line-height: 80px;
          border-top: 2px #f1f1f1 solid;
        }
        .l2 {
          line-height: 40px;
          padding: 14px 0;
        }
        .l3 {
          line-height: 40px;
          padding: 20px 0;
        }
      }
      .virtual-info {
        margin-top: 12px;
      }
      .total {
        margin-top: 50px;
        font-size: 34px;
      }
      .btns {
        margin-top: 103px;
        a {
          width: 330px;
          height: 82px;
          border: 2px #f1f1f1 solid;
          line-height: 82px;
          font-size: 26px;
        }
        a + a {
          margin-left: 18px;
        }
      }
    }
  }
  .mypage-order {
    padding-top: 39px;
    padding-bottom: 39px;
    .my-order-check {
      width: 602px;
      padding: 38px 38px 50px;
      .tit-top {
        margin-bottom: 24px;
        h3 {
          font-size: 30px;
        }
        .btn-guide {
          font-size: 26px;
          background-size: 24px 24px;
          padding-right: 35px;
        }
      }
      p {
        font-size: 26px;
        line-height: 34px;
        margin-bottom: 35px;
      }
      .cs-depth3 {
        ul {
          margin: 44px 0 51px;
        }
      }
      .btn-inquiry {
        a {
          padding: 18px 0 23px;
          font-size: 32px;
          border: 2px solid #f1f1f1;
        }
      }
      select {
        background-size: 22.5px;
        font-size: 28px;
        line-height: 81px;
        border-bottom: 2px #eee solid;
      }
      .sct-box {
        select {
          width: 276px;
        }
        select + select {
          margin-left: 42px;
        }
      }
      .add-prod {
        margin-top: 29px;
        select {
          width: 143px;
        }
        input {
          width: 421px;
          margin-left: 32px;
          border-bottom: 2px #eee solid;
          line-height: 81px;
          font-size: 28px;
        }
      }
    }
    .order-number {
      padding: 82px 0 62px;
      p {
        font-size: 34px;
      }
    }
    .order {
      margin-top: 26px;
      .d-info {
        li:last-child {
          border-bottom: 0;
          padding: 36px 0 87px;
        }
      }
      .d-top {
        h3 {
          font-size: 30px;
        }
      }
    }
    .complete {
      padding: 0 36px 69px;
      &.return-delivery {
        .dl-table {
          dd {
            &.l3 {
              line-height: 45px;
            }
          }
        }
      }
    }
    .btn-back {
      padding-bottom: 45px;
      a {
        width: 678px;
        height: 80px;
        border: 1px #f1f1f1 solid;
        font-size: 26px;
        line-height: 80px;
      }
    }
    .order.return-pickup {
      .d-top {
        border-top: 6px #000 solid;
      }
    }
    .order.cancel-gift {
      .d-top {
        padding: 69px 36px 64px;
        p {
          font-size: 24px;
          margin-top: 17px;
          letter-spacing: -1px;
        }
      }
    }
    .more {
      width: 58px;
      height: 58px;
      margin: 16px auto 11px auto;
    }
  }
  .order-notice {
    min-height: 1158px;
    h3 {
      font-size: 28px;
      margin: 55px 0 47px;
      padding: 0 36px;
    }
    .notice {
      padding: 44px 56px 64px;
      li {
        font-size: 26px;
        line-height: 44px;
        letter-spacing: -1px;
      }
    }
  }
  .h1-top {
    .pop-close {
      top: 40px;
      right: 36px;
      width: 30px;
      height: 30px;
    }
    .pop-close.close2 {
      top: 48px;
    }
    h1 {
      font-size: 34px;
      padding: 40px 0 0;
    }
  }
}
